body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Definte Custom Fonts */
@font-face {
    font-family: 'circular-std-black';
    src: url(/static/media/CircularStd-Black.b5fa1b55.woff) format('woff');
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: 'circular-std-black-italic';
    src: url(/static/media/CircularStd-BlackItalic.2153a12d.woff) format('woff');
    font-weight: 900;
    font-style: italic;
}

@font-face {
    font-family: 'circular-std-bold';
    src: url(/static/media/CircularStd-Bold.6f4fe3ca.woff) format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'circular-std-bold-italic';
    src: url(/static/media/CircularStd-BoldItalic.8d5dbe81.woff) format('woff');
    font-weight: bold;
    font-style: italic;
}

@font-face {
    font-family: 'circular-std-medium';
    src: url(/static/media/CircularStd-Medium.e47ea4c0.woff) format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'circular-std-medium-italic';
    src: url(/static/media/CircularStd-MediumItalic.eddb7021.woff) format('woff');
    font-weight: 500;
    font-style: italic;
}

@font-face {
    font-family: 'circular-std-book';
    src: url(/static/media/CircularStd-Book.c47d7e58.woff) format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'circular-std-book-italic';
    src: url(/static/media/CircularStd-BookItalic.cc06409e.woff) format('woff');
    font-weight: normal;
    font-style: italic;
}

/* Global Styles */

.App {
  width: 784px;
  margin-left: auto;
  margin-right: auto;
}

/* Header Styles */

.header {
  width: 100%;
  margin-top: 115px;
  margin-bottom: 76px;
  font-family: circular-std-medium, Arial, sans-serif;
}

.header .navigation {
  width: 100%;
  display: -webkit-flex;
  display: flex;
}

.header .navigation div a {
  color: #BBBBBB;
  text-decoration: none;
  transition: color 0.2s;
}

.header .navigation div a:hover {
  color: black !important;
}

.header .navigation div a:visited {
  color: inherit;
}

.header .navigation-sub {
  width: 276px;
  font-size: 22px;
  display: -webkit-flex;
  display: flex;
}

.header .navigation .anne {
  font-size: 26px;
  width: calc(100% - 276px);
  line-height: 36px;
}

.header .navigation .navigation-sub .active {
  color: black;
}

.header .navigation .navigation-sub .inactive {
  color: #BBBBBB;
}

.header .navigation .navigation-sub .work, .articles {
  padding-right: 40px;
}

.header .designer {
  font-size: 26px;
  line-height: 36px;
}

.header .speciality {
  font-size: 20px;
  color: #02CF5D;
  line-height: 36px;
}

/* Footer */
.footer {
  margin-top: 60px;
  margin-bottom: 60px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
}

.footer .footer-statement {
  font-family: circular-std-medium, Arial, sans-serif;
  font-size: 18px;
  line-height: 28px;
  margin: 0px auto 11px auto;
  width: 100%;
}

.footer .social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.footer .social-links .social {
  background-color: #000000;
  border-radius: 32px;
  width: 32px;
  height: 32px;
  margin-right: 8px;
}

.footer .social-links .social-last {
  background-color: #000000;
  border-radius: 32px;
  width: 32px;
  height: 32px;
  margin-right: 0px;
}

.footer .social-links .social .behance {
  width: 19px;
  height: 12px;
  padding: 10px 6.5px 10px 6.5px;
}

.footer .social-links .social .linkedin {
  width: 16px;
  height: 14px;
  padding: 8px 8px 10px 8px;
}

.footer .social-links .social .medium {
  width: 16px;
  height: 12px;
  padding: 10px 8px 10px 8px;
}

.footer .social-links .social-last .email {
  width: 16px;
  height: 12px;
  padding: 10px 8px 10px 8px;
}

.footer .footer-copyright {
  font-family: circular-std-book, Arial, sans-serif;
  font-size: 14px;
  color: #B1B1B1;
  line-height: 36px;
  margin: 20px auto 0px auto;
  width: 100%;
}


/* MAIN PAGE */

/* Project List */

.projects {
  width: 100%;
  font-family: circular-std-medium, Arial, sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.projects .left {
  width: 369px;
}

.projects .left .projects-intro {
  font-family: circular-std-book;
  margin-top: 0px;
  margin-bottom: 58px;
  font-size: 18px;
  line-height: 28px;
}

.projects .left .projects-intro .bold {
  font-family: circular-std-medium, Arial, sans-serif;
}

.projects .right {
  width: 369px;
}

/* Individual Project */
.project a {
  text-decoration: none;
  color: inherit;
}

.project a:visited {
  color: black;
}

.hmw {
  position: absolute;
  z-index: 10;
  opacity: 0;
  transition: opacity 0.4s;
}

.show {
  opacity: 1;
}

.change-color-0 {
  color: #0B9C96;
}

.change-color-1 {
  color: #E96F5E;
}

.change-color-2 {
  color: #FBC060;
}

.change-color-3 {
  color: #72A4EE;
}

.change-scale {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.hmw .hmw-background {
  width: 369px;
  height: 369px;
  text-align: center;
  font-family: circular-std-medium, Arial, sans-serif;
  color: #FFFFFF;
  font-size: 20px;
  line-height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.project-default {
  width: 100%;
  margin-bottom: 52px;
}

.project-last {
  width: 100%;
  margin-bottom: 0px;
}

.project-image {
  width: 369px;
  height: 369px;
  overflow: hidden;
  margin-bottom: 19px;
  box-shadow: 0px 0px 20px #E8E8E8;
}

.project-image-size {
  width: 369px;
  height: 369px;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
}

.project-title {
  font-size: 18px;
  line-height: 28px;
  transition: color 0.4s;
}

.project-category {
  font-family: circular-std-book, Arial, sans-serif;
  font-size: 18px;
  line-height: 28px;
  transition: color 0.4s;
}


/* ARTICLES PAGE */

/* Articles List */

.articles {
  width: 100%;
  font-family: circular-std-medium, Arial, sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.articles .left {
  width: 369px;
}

.articles .left .articles-intro {
  font-family: circular-std-book;
  margin-top: 0px;
  margin-bottom: 58px;
  font-size: 18px;
  line-height: 28px;
}

.articles .left .articles-intro .bold {
  font-family: circular-std-medium, Arial, sans-serif;
}

.articles .right {
  width: 369px;
}

/* Individual Article */

.article a {
  text-decoration: none;
  color: inherit;
}

.article a:visited {
  color: black;
}

.article-default,
.article-last {
  width: 100%;
  height: 369px;
  box-sizing: border-box;
  border: 1px solid #E6E6E6;
  box-shadow: 0px 0px 20px rgba(217, 217, 217, 0.2);
}

.article-default {
  margin-bottom: 52px;
}

.article-last {
  margin-bottom: 0px;
}

.article-image {
  width: 367px;
  height: 223px;
  overflow: hidden;
}

.article-image-size {
  width: 367px;
  height: 223px;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
}

.article-contents {
  padding: 19px 25px 19px 25px;
}

.article-title {
  font-family: circular-std-medium, Arial, sans-serif;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 6px;
}

.article-subtitle {
  font-family: circular-std-book, Arial, sans-serif;
  font-size: 16px;
  line-height: 22px;
}

.article-date {
  color: #A0A0A0;
  font-family: circular-std-book, Arial, sans-serif;
  font-size: 12px;
  line-height: 28px;
}

.one-line {
  margin-top: 21px;
}

.two-line {
  margin-top: 5px;
}


/* ABOUT PAGE */

.about-image {
  width: 784px;
  height: 522px;
  box-shadow: 0px 0px 10px rgba(224, 224, 224, 0.5);
}

.about-content {
  margin-top: 50px;
  font-family: circular-std-book, Arial, sans-serif;
  font-size: 18px;
  line-height: 21.5px;
}

.about-title {
  font-family: circular-std-bold, Arial, sans-serif;
  font-size: 20px;
}

